<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">报修列表</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="$router.push({path:'/editRepair'})">报修</span>
        </div>
        <div style="padding:0.84rem 0 1.7rem;">
            <div class="pl-30">
                <ul>
                    <li class="pr-30 b_line" v-for="(item,index) in list" :key="index" @click="lookDetail(index,item.id)">
                        <div>
                            <p class="f28 c333">{{item.classesName}}</p>
                            <!-- <img :style="{display:item.isSee?'none':'inline-block'}" src="../../../assets/message/notSee.png" alt=""> -->
                        </div>
                        <div class="mt-22">
                            <p class="f26 c666" style="overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width:80%;">{{item.labelName}}</p>
                            <p class="f22 c999">{{item.createTime | substr(item.createTime)}}</p> 
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            list:[
            ],
            param : {
                schoolId:JSON.parse(localStorage.getItem('zhxy_user')).schoolId,
                teacherId:JSON.parse(localStorage.getItem('zhxy_user')).id,
                state : 1,
                page : 1,
                size : 1
            }
        }
    },
    filters : {
        substr(str){
           return str.substring(0,10)
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        not(){
            this.active=true
            this.active1=false
        },
        yes(){
            this.active=false
            this.active1=true
        },
        lookDetail(index,id){
            var list=this.list;
            list[index].isSee=true;
            this.list=list;
            this.$router.push({path:'/repairDetail',query:{id:id}})
        },
        getUserEquipment(){//设备报修列表
            this.$http({url:this.$api.message.getUserEquipment},this.param).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                }
            })
        }
    },
    created() {
        this.getUserEquipment();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    
    li{
        padding:0.3rem 0.3rem 0.2rem 0;
    }
    li div{
        display: flex;
        justify-content: space-between;
        align-items: center
    }
    li img{
        width:0.14rem;
        height:0.14rem;
    }
</style>




